import './newsWindow.css'
import { useDispatch, useSelector } from 'react-redux'
import { openGlobalWindowAction } from '../../store/windowReduser'
import WindowTitleComponent from '../windowTitleComponent/windowTitleComponent'
import NewItem from '../newItem/newItem'
import CopyrightComponent from '../copyrightComponent/copyrightComponent'

function NewsWindow(props){
    const {funcs, vars} = props
    const newsCount = 1

    const dispatch = useDispatch()
    const openMain = () =>{
        dispatch(openGlobalWindowAction(1))
      }
    
    const openNew = useSelector(state=>state.news.newOpen)
    const news = useSelector(state=>state.news.news)

    return(
        <div className='window'>
            <WindowTitleComponent/>
            <article id='newsWindow'>
                {newsCount<=0? (
                    <section className='newsNull'>
                        <p>Новин поки немає ;(</p>
                        <button onClick={openMain}>На головну</button>
                    </section>
                )
                :
                (
                    <>
                    {openNew <= 0? (
                        <>
                            {Object.values(news).map((item, idx) => (<NewItem vars={{id:idx+1, type:'half'}}/>))}
                            {/* <NewItem vars={{id:1, type:'half'}}/>
                            <NewItem vars={{id:2, type:'half'}}/> */}
                        </>
                    ):
                    (
                        <>
                            <NewItem vars={{id:openNew, type:'full'}}/>
                        </>
                    )}
                    </>
                )}
            </article>
            <CopyrightComponent vars={{color:'black'}}/>
        </div>
    )
}

export default NewsWindow