export async function sendContact(formData){
    try {
        const response = await fetch('api/support/contackMe/', {
          method: 'POST',
          body: formData,
        });
    
        if (response.ok) {
          const data = await response.json();
          return true
        } else {
          return false
        }
      } catch (error) {
        return false
      }
}