const defaultState = {

    openCategoryId: 0,
    openSubCategoryId: 0,

    categoryies: []
}


const SET_MENU = 'SET_MENU'
const SET_CATEGORY = 'SET_CATEGORY'
const SET_SUB_CATEGORY = 'SET_SUB_CATEGORY'

export const menuReducer = (state = defaultState, action) => {
    switch (action.type) {
        case SET_MENU:
            return {...state, categoryies: action.payload}
            
        case SET_CATEGORY:
            return {...state, openCategoryId: action.payload, openSubCategoryId: 0}
        case SET_SUB_CATEGORY:
            console.log(state.openCategoryId, action.payload);
            return {...state, openSubCategoryId: action.payload}
    
        default:
            return state
    }
}

export const setMenu = (payload) => ({type:SET_MENU, payload})
export const setCategory = (payload) => ({type:SET_CATEGORY, payload})
export const setSubCategory = (payload) => ({type:SET_SUB_CATEGORY, payload:payload})