import './menuWindow.css'
import GoodItem from '../goodItem/goodItem'
import FooterMenu from '../footerMenu/footerMenu'
import WindowTitleComponent from '../windowTitleComponent/windowTitleComponent'
import { useEffect,useState, useRef } from 'react';

import { useDispatch, useSelector } from 'react-redux'
import { setCategory, setSubCategory } from '../../store/menuReduser'

function MenuWindow(props){
    const {funcs, vars} = props

    const dispatch = useDispatch()

    const openCategoryId = useSelector(state => state.menu.openCategoryId)
    const openSubCategoryId = useSelector(state => state.menu.openSubCategoryId)

    const categoryies = useSelector(state => state.menu.categoryies)

    const basketGoods = useSelector(state => state.basket.basketGoods)

    const categoriesList = categoryies.map(category => category) || []
    const subCategoriesList = categoryies[openCategoryId]?.subCategoryies?.map(subCategory => subCategory) || []
    const subCategoriesGoodsList = subCategoriesList[openSubCategoryId]?.goods?.map(subCategoryGoods => subCategoryGoods) || []

    console.log(subCategoriesGoodsList);

    const changeCategory = (id) => {
        dispatch(setCategory(id))
    }
    const changeSubCategory = (id) => {
        dispatch(setSubCategory(id))
    }
    const refToElement = useRef(null);

    const scrollToElement = () => {
        refToElement.current.scrollTo({
            left: refToElement.current.scrollLeft - 1000,
            behavior: 'smooth',
          });
    }
    useEffect(()=>{
        localStorage.setItem('basket', JSON.stringify(basketGoods));
    },[basketGoods])


    //анімація
    const animationQueueList = [0,1,2,3,4,5,6]
    const [animationQueue, setAnimationQueue] = useState(0)
    async function startAnimation(type){
        switch (type) {
            case 'open':
                for (let i = 0; i < animationQueueList.length; i++) {
                    const item = animationQueueList[i];
                    await new Promise((resolve) => setTimeout(resolve, 50)); // Затримка на 50мс
                    setAnimationQueue(item);
                }
                break;
            case 'close':
                for (let i = animationQueueList.length - 1; i >= 0; i--) {
                    const item = animationQueueList[i];
                    await new Promise((resolve) => setTimeout(resolve, 50)); // Затримка на 50мс
                    setAnimationQueue(item);
                }
                break;
        
            default:
                break;
        }
    }

    useEffect(()=>{
        //запуск анімації
        startAnimation('open')
    },[])
    

    return(
        <div className='window'>
            <article id='menuLists'>
                <section showfrom={animationQueue<1?'left':''} elstatus={animationQueue<1?'hide':'show'} className='menuList'>
                    {categoriesList.map((category, idx)=><button onClick={()=>{changeCategory(idx);scrollToElement();}} className={`f-1 ${openCategoryId===idx?'select':''}`}>{category.title}</button>)}
                </section>
                {subCategoriesList.length > 0 ? 
                    <section showfrom={animationQueue<2?'right':''} elstatus={animationQueue<2?'hide':'show'} ref={refToElement} className='menuList'>
                        {subCategoriesList.map((subCategory, idx)=><button onClick={()=>{changeSubCategory(idx)}} className={`f-1 ${openSubCategoryId===idx?'select':''}`}>{subCategory.title}</button>)}
                    </section>
                :
                    <></>
                }
            </article>
            
            {subCategoriesList[openSubCategoryId] !== undefined && <WindowTitleComponent vars={{title: subCategoriesList[openSubCategoryId].title}}/>}
            <article showfrom={animationQueue<4?'bottom':''} elstatus={animationQueue<4?'hide':'show'} id='menuWindow'>
            {/* <WindowTitleComponent vars={{title: subCategoriesList[openSubCategoryId].title}}/> */}
                {subCategoriesGoodsList.length<=0?
                    <p className='f-1'>Скоро буде доступно ;)</p>
                :
                    subCategoriesGoodsList.map((good, idx) => <GoodItem vars={{type:'menu', title: good.title, description: good.description, price: good.price, img: good.image, category:openCategoryId, subCategory:openSubCategoryId, goodId:idx }}/>)
                }
            </article>
            <FooterMenu/>
        </div>
    )
}

export default MenuWindow