import './mainWindow.css'
import image from '../../images/1.jpeg'

import FooterMenu from '../footerMenu/footerMenu'
import Galary from '../galary/galary'
import { useEffect, useState } from 'react'
import { useDispatch } from 'react-redux'

import { openGlobalWindowAction } from '../../store/windowReduser'

function MainWindow(props){
    const {funcs, vars} = props

    const dispatch = useDispatch()
    
    async function openMenu(){
        await startAnimation('close')
        await new Promise((resolve) => setTimeout(resolve, animationQueueList.length*70)); // Затримка на 50мс
        dispatch(openGlobalWindowAction(6))
      }

    //анімація
    const animationQueueList = [0,1,2,3,4,5,6]
    const [animationQueue, setAnimationQueue] = useState(0)
    async function startAnimation(type){
        switch (type) {
            case 'open':
                for (let i = 0; i < animationQueueList.length; i++) {
                    const item = animationQueueList[i];
                    await new Promise((resolve) => setTimeout(resolve, 50)); // Затримка на 50мс
                    setAnimationQueue(item);
                }
                break;
            case 'close':
                for (let i = animationQueueList.length - 1; i >= 0; i--) {
                    const item = animationQueueList[i];
                    await new Promise((resolve) => setTimeout(resolve, 50)); // Затримка на 50мс
                    setAnimationQueue(item);
                }
                break;
        
            default:
                break;
        }
    }

    useEffect(()=>{
        //запуск анімації
        startAnimation('open')
    },[])
    

    return(
        <article id='mainWindow'>
            <Galary vars={{itemsCount:3}}>
                <div className='galaryItem' >
                    <img src={image} alt='wef'></img>
                    <p>Поспішайте в Інформатику за шматочками цього торта</p>
                </div>
                <div className='galaryItem' >
                    <img src={image} alt='wef'></img>
                    <p>Поспішайте в Інформатику за шматочками цього торта</p>
                </div>
                <div className='galaryItem' >
                    <img src={image} alt='wef'></img>
                    <p>Поспішайте в Інформатику за шматочками цього торта</p>
                </div>
            </Galary>
            <section className='mainSection'>
                <button onClick={()=>{openMenu()}} className='f-1' showfrom={animationQueue<1?'bottom':''} elstatus={animationQueue<1?'hide':'show'} >
                    <p>Їжа</p><i className="material-icons f-2">arrow_forward</i>
                </button>
                <button onClick={()=>{openMenu()}} className='f-1'  showfrom={animationQueue<2?'bottom':''} elstatus={animationQueue<2?'hide':'show'}>
                    <p>Напої</p><i className="material-icons f-2">arrow_forward</i>
                </button>
                <button onClick={()=>{openMenu()}} className='f-1'  showfrom={animationQueue<3?'bottom':''} elstatus={animationQueue<3?'hide':'show'}>
                    <p>Товари</p><i className="material-icons f-2">arrow_forward</i>
                </button>
                <button onClick={()=>{openMenu()}} className='f-1'  showfrom={animationQueue<4?'bottom':''} elstatus={animationQueue<4?'hide':'show'}>
                    <p>Виставки</p><i className="material-icons f-2">arrow_forward</i>
                </button>
                <button onClick={()=>{openMenu()}} className='f-1'  showfrom={animationQueue<5?'bottom':''} elstatus={animationQueue<5?'hide':'show'}>
                    <p>Музика</p><i className="material-icons f-2">arrow_forward</i>
                </button>
            </section>
            <FooterMenu/>
        </article>
    )
}

export default MainWindow