import { useDispatch, useSelector } from 'react-redux';
import { setLoadingOffAction } from './store/windowReduser';
import { setBasketFromLocalStoreAction } from './store/basketReduser';
import { updateOrdersFromLocalAction } from './store/basketReduser';
import './App.css';

import NavigationMenu from './components/navigationMenu/navigationMenu';
import HeaderMenu from './components/headerMenu/headerMenu';
import MainWindow from './components/mainWindow/mainWindow';
import BasketWindow from './components/basketWindow/basketWindow';
import AboutWindow from './components/aboutWindow/aboutWindow';
import NewsWindow from './components/newsWindow/newsWindow';
import СontackWindow from './components/contactWindow/contackWindow'
import MenuWindow from './components/menuWindow/menuWindow';
import Loading from './components/loadingWindow/loading';
import FeedbackWindow from './components/feedbackWindow/feedbackWindow';

import { getMenuFromPoster } from './servises/menuServises';

import { useEffect, useState } from 'react';

import { openGlobalWindowAction } from './store/windowReduser';


function App() {
  const searchParams = new URLSearchParams(window.location.search);
  const feedback = searchParams.get("feedback");
  // feedback && dispatch(openGlobalWindowAction(2))

  const dispatch = useDispatch()
  feedback && dispatch(openGlobalWindowAction(7))

  const [navMenuActive, setNavMenuActive] = useState(false)
  const globalWindowOpen = useSelector(state => state.window.globalWindowOpen)
  const openBasket = () =>{
    dispatch(openGlobalWindowAction(2))
  }

  const loadingStatus = useSelector(state => state.window.loadingStatus)
  useEffect(()=>{

    getMenuFromPoster(dispatch)


    dispatch(setBasketFromLocalStoreAction())
    dispatch(updateOrdersFromLocalAction())
    setTimeout(() => {
      dispatch(setLoadingOffAction())
    }, 1000 + Math.random()*2400);
  },[])
  return (
    <div className="App">
      {loadingStatus?<Loading/>:(
        <>
          {window.innerWidth < 500? (
            <>
                {navMenuActive && <NavigationMenu funcs={{closeNavMenu:setNavMenuActive}}/>}
                <HeaderMenu funcs={{openNavMenu:setNavMenuActive}}/>
                {globalWindowOpen===1 && 
                  <MainWindow/>
                }
                {globalWindowOpen===2 && 
                  <BasketWindow/>
                }
                {globalWindowOpen===3 && 
                  <AboutWindow/>
                }
                {globalWindowOpen===4 && 
                  <NewsWindow/>
                }
                {globalWindowOpen===5 && 
                  <СontackWindow/>
                }
                {globalWindowOpen===6 && 
                  <MenuWindow/>
                }
                {globalWindowOpen===7 && 
                  <FeedbackWindow/>
                }
            </>
          )
        :
          (
            <article className='window-alert'>
              <h1 style={{textAlign:'center', fontSize:'40px'}}>Упс... для таких великих екранів я ще не готовий, відкрий мене у телефоні</h1>
              <i style={{textAlign:'center', fontSize:'100px'}} className="material-icons f-2">error_outline</i>
            </article>
          )}
        </>
      )}
    </div>
  );
}

export default App;
