import './contackWindow.css'
import mapImage from '../../images/map.jpeg'

import WindowTitleComponent from '../windowTitleComponent/windowTitleComponent'
import CopyrightComponent from '../copyrightComponent/copyrightComponent'
import ModalWindow from '../modalWindow/modalWindow'

import { useState } from 'react' 

import { sendContact } from '../../servises/contactServises'

function СontackWindow(props){
    const {funcs, vars} = props

    const [alertModal, setAlertModal] = useState(null)

    const [username, setUsername] = useState('')

    const [phoneMask, setPhoneMask] = useState('')
    const [phone, setPhone] = useState('')

    const [phoneValidClass, setPhoneValidClass] = useState('')
    const codes = ['39','50','63','66','67','68','91','92','93','94','95','96','97','98','99']
    
    const changePhone = (num) => {
        let mask = num.replace(/[^+\d]/g, '')
        // mask = mask.replace('', '')
        mask = (mask[0]+mask[1])==='38'?mask.replace('38', ''):mask
        mask = mask.split('').map((char,idx)=>{
            switch (idx) {
                case 0:
                    return '('+char
                case 3:
                    return ')'+char
                case 6:
                    return '-'+char
                case 8:
                    return '-'+char
            
                default:
                    return char
            }
        })

        mask = mask.join('')
        console.log( codes.indexOf((mask[2]+mask[3])) === -1);
        if(mask==='')setPhoneValidClass('')
        else if(mask[1]!='0' || codes.indexOf((mask[2]+mask[3])) === -1)setPhoneValidClass('false')
        else if(mask.split('').length<14)setPhoneValidClass('false')
        else setPhoneValidClass('true')

        setPhoneMask(mask)
    }

    async function handleContactSubmit(e){
        e.preventDefault()
        if(phoneValidClass==='false' ){
            setAlertModal(<ModalWindow vars={{type:'alert', title:'Введіть вірний номер телефону!',choices:[{title:'Добре', value:true}] }} funcs={{closeModal:()=>{setAlertModal(null)}}}/>)
            return
        }

        const formData = new FormData();
        formData.append('name', username);
        formData.append('phone', '+380 '+phoneMask);

        const sendResult = await sendContact(formData)

        sendResult?
            setAlertModal(<ModalWindow vars={{type:'alert', title:'Ми Вам зателефонуємо!',choices:[{title:'Добре', value:true}] }} funcs={{closeModal:()=>{setAlertModal(null)}}}/>)
            :
            setAlertModal(<ModalWindow vars={{type:'alert', title:'Щось пішло не по плану :(',choices:[{title:'Добре', value:true}] }} funcs={{closeModal:()=>{setAlertModal(null)}}}/>)
    }

    return(
        <div className='window'>
            <WindowTitleComponent/>
            <article id='contackWindow'>
                {alertModal!==null&&alertModal}

                <section>
                    <h1 className='f-2'>Де?</h1>
                    <p>Контрактова площа,<br/> вул Воздвиженська 36</p>
                </section>
                <section>
                    <h1 className='f-2'>Коли?</h1>
                    <p>Пн-Нд: 8:00-22:00 </p>
                </section>
                <section>
                    <h1 className='f-2'>Контакти</h1>
                    <p className='f-0'>+38(044) 123-45-67<br/><a href='/'>informatioca@gmail.com</a></p>
                </section>
                <section>
                    <a className='f-0' href='/'>Telegram</a>
                    <a className='f-0' href='/'>Instagram</a>
                </section>

                <img src={mapImage} alt='map'></img>
                <form onSubmit={(e)=>{handleContactSubmit(e)}}>
                    <input className='f-0' required value={username} onChange={(e)=>{setUsername(e.target.value)}} type='text' placeholder="Ваше им'я"></input>
                    <input required className={`f-0 ${phoneValidClass}`} value={phoneMask} onChange={(e)=>{changePhone(e.target.value)}} type='text' maxLength='14' placeholder='(097) 000-00-00'></input>
                    <button className='f-0' type='submit'>Зв'язатись з нами</button>
                </form>
            </article>
            <CopyrightComponent vars={{color:'black'}}/>
        </div>
    )
}

export default СontackWindow