import { useDispatch,useSelector } from 'react-redux'

import './headerMenu.css'

import { openGlobalWindowAction } from '../../store/windowReduser'

function HeaderMenu(props){
    const {funcs, vars} = props
    const {openNavMenu} = funcs

    const dispatch = useDispatch()
    const openBasket = () =>{
        dispatch(openGlobalWindowAction(2))
      }
      
    const basketGoodsCount = useSelector(state => state.basket.basketGoods.length)
    

    return(
        <header id='headerMenu'>
            <svg width="5vh" height="5vh" viewBox="0 0 90 215" fill="none" xmlns="http://www.w3.org/2000/svg">
                <rect y="125" width="90" height="90" fill="black"/>
                <circle cx="45" cy="45" r="45" fill="black"/>
            </svg>
            <section className='menuSection'>
                <button className='f-1' onClick={openBasket}>ко шик<div className='notify'>{basketGoodsCount}</div></button>
                <button className='f-1' onClick={()=>{openNavMenu(true)}}>ме ню</button>
            </section>
        </header>
    )
}

export default HeaderMenu