import { addOrderAction, updateOrdersAction,clearOrdersAction } from "../store/basketReduser"

export async function checkOrdersStatus(dispatch, orders){
    const invoiceIds = orders.map(order => order.invoceId)
    //відправляє замовлення на севрет та очикує json з усіма замовленнями
    try {
        const formData = new FormData();
        formData.append('orders', JSON.stringify(invoiceIds));

        const response = await fetch('api/menu/orderStatus/', {
            method: 'POST',
            body: formData
        });
        const jsonAnswer = await response.json()

        //відправляє отримані статуси на обробку
        const resultOfStatusProcess = orderStatusProcess(jsonAnswer ,orders)
        dispatch(updateOrdersAction(resultOfStatusProcess))
      } catch (error) {
        console.error(error);
      }
}

function orderStatusProcess(receivedStatuses, oldStatuses) {
    let filteredOrders = oldStatuses.map((orderStatus) => {
      const receivedOrder = receivedStatuses.find((receivedStatus) => receivedStatus.invoiceId === orderStatus.invoiceId);
      if (receivedOrder) {
        if (receivedOrder.orderStatus === 'prepeading'){
            sendNewOrderToPoster(orderStatus)
        }
        return { ...orderStatus, id: receivedOrder.orderId, status: receivedOrder.orderStatus };
      } else {
        return { ...orderStatus,  status: "done" };
      }
    });
    return filteredOrders
  }

export async function createOrder(type, dispatch, orderData){
    // orderData включає в себе позиції в кошику та позиції в меню. адже в кошику зберігається лише к-сть та id товару
    // який потім береться із загального меню, щоб вивестись.
    // тому для генерування чеку для різних банків, використовуватиметься окремий метод
    const newOrder = generateСheck(type, orderData)
    const sendNewOrderResult = await sendNewOrderToServerAndGetPayLink(type, newOrder)
    dispatch(addOrderAction({...newOrder, invoceId:sendNewOrderResult.response.invoiceId}))
    window.location.href = sendNewOrderResult.response.pageUrl;
}

const generateСheck = (type, orderData) => {
    //type - має містити тип чеку який потрібно згенерувати, як от 'monobanl' 'bitcoin' ...ect
    //відносно цього буде вібдуватись ченерація нового чеку, адже для різних платформ, потрібно
    //відправляти різні види json, для генерації чеку
    switch (type) {
        case 'monobank':
            const {basketGoods, menuCategoryies} = orderData
            //генерує рандомний id, згодом його можна буде отримувати із сервера або poster
            // const generateId = Math.floor(Math.random()*10000)
            let amount = 0

            const newOrder = {
                id: 0,
                status: 'pay',
                payType: type,
                monobankCheckGoods: basketGoods.map((basketGood)=>{
                    const good = menuCategoryies[basketGood.category].subCategoryies[basketGood.subCategory].goods[basketGood.goodId]
                    amount = amount + (basketGood.count*good.price)
                    return{
                            "name": good.title,
                            "qty": basketGood.count,
                            "sum": (basketGood.count*good.price),
                            "icon": "string",
                            "unit": "шт.",
                            "code": "d21da1c47f3c45fca10a10c32518bdeb",
                            "barcode": "3b2a558cc6e44e218cdce301d80a1779",
                            "header": "Хідер",
                            "footer": "Футер",
                            "tax": [
                                0
                            ],
                            "uktzed": "uktzedcode"
                        }
                }),
                criptoCheckGoods: '',
                posterCheckGoods: basketGoods.map((basketGood)=>{
                    const good = menuCategoryies[basketGood.category].subCategoryies[basketGood.subCategory].goods[basketGood.goodId]
                    return{
                        "product_id": good.posterProductId,
                        "count": basketGood.count
                    }
                })
            }
            return {...newOrder, amount:amount}

        case 'bitcoin':
            return null
    
        default:
            return null
    }
}

//відправляє нове замовлення на сервет та отримує посилання на оплату
async function sendNewOrderToServerAndGetPayLink(type, newOrder){
    const formData = new FormData();
    switch (type) {
        case 'monobank':
            formData.append('type', 'monobank');
            formData.append('destination', 'Нове замовлення');
            formData.append('amount', newOrder.amount);
            formData.append('basketOrder', JSON.stringify(newOrder.monobankCheckGoods));
            formData.append('redirectUrl', 'http://127.0.0.1:3000/');
            const response = await fetch('api/menu/createOrder/', {
                                method: 'POST',
                                body: formData
                            })
            const jsonAnswer = await response.json()
            console.log(jsonAnswer);
            return jsonAnswer
    
        default:
            break;
    }
}


async function sendNewOrderToPoster(newOrder){
    const formData = new FormData();
    formData.append('invoceId', newOrder.invoceId);
    formData.append('products', JSON.stringify(newOrder.posterCheckGoods));
    const response = await fetch('api/menu/createPosterOrder/', {
                        method: 'POST',
                        body: formData
                    })
    const jsonAnswer = await response.json()
    console.log(jsonAnswer);
    return jsonAnswer
}

export async function deleteOrderFromServer(orderId){
    const formData = new FormData();
    formData.append('orderId', orderId);
    const response = await fetch('api/menu/deleteOrder/', {
                        method: 'POST',
                        body: formData
                    })
    const jsonAnswer = await response.json()
    console.log(jsonAnswer);
    return jsonAnswer
}