import image from '../../images/1.jpeg'

import { useState, useEffect } from 'react'
import './goodItem.css'
import { useDispatch, useSelector } from 'react-redux'
import { increaceGoodCountAction, reduceGoodCountAction, deleteGoodFromBasketAction, addGoodToBasketAction } from '../../store/basketReduser'

function GoodItem(props){
    const {funcs, vars} = props
    const {type, title, description, price, img} = vars
    const {category, subCategory, goodId} = vars



    const dispatch = useDispatch()
    const basketGoods = useSelector(state => state.basket.basketGoods)

    const goodInBasketItem = basketGoods.find(item => Object.keys({category, subCategory, goodId}).every(key => item[key] === {category, subCategory, goodId}[key]));

    const isGoodInBasket = goodInBasketItem===undefined?false:true

    const count = goodInBasketItem===undefined?0:goodInBasketItem.count

    // const [price, setPrice] = useState(150)

    const addGoodToBasket = () => {
        dispatch(addGoodToBasketAction({category, subCategory, goodId, count:1}))
    }
    const removeGoodToBasket = () => {
        dispatch(deleteGoodFromBasketAction({category, subCategory, goodId}))
    }
    const reduceGoodCount = () => {
        if(count>1)dispatch(reduceGoodCountAction({category, subCategory, goodId}))
        else return
    }
    const increaceGoodCount = () => {
        dispatch(increaceGoodCountAction({category, subCategory, goodId}))
    }



    //анімація
    const animationQueueList = [0,1,2,3]
    const [animationQueue, setAnimationQueue] = useState(0)
    async function startAnimation(type){
        switch (type) {
            case 'open':
                for (let i = 0; i < animationQueueList.length; i++) {
                    const item = animationQueueList[i];
                    await new Promise((resolve) => setTimeout(resolve, 50)); // Затримка на 50мс
                    setAnimationQueue(item);
                }
                break;
            case 'close':
                for (let i = animationQueueList.length - 1; i >= 0; i--) {
                    const item = animationQueueList[i];
                    await new Promise((resolve) => setTimeout(resolve, 50)); // Затримка на 50мс
                    setAnimationQueue(item);
                }
                break;
        
            default:
                break;
        }
    }

    useEffect(()=>{
        //запуск анімації
        startAnimation('open')
    },[])


    return(
        <section id='goodItem' className={type==='menu'?'line':''}>
            <div  showFrom={animationQueue<1?'bottom':''} elStatus={animationQueue<1?'hide':'show'} className='sec-1'>
                <div className='description'>
                    <h1 className='f-1'>{title}</h1>
                    <p className='f-0'>{description}</p>
                </div>
                <img src={'https://joinposter.com'+img} alt='icon'></img>
            </div>
            <div showFrom={animationQueue<2?'bottom':''} elStatus={animationQueue<2?'hide':'show'} className='sec-2'>
                <div className='price'>
                    <p className='f-0'>{price/100}</p> <p>UAH</p>
                </div>
                {type==='basket' && (<button onClick={removeGoodToBasket} className='delete f-0'>Видалити</button>)}
            </div>
            <div showFrom={animationQueue<3?'bottom':''} elStatus={animationQueue<3?'hide':'show'} className='sec-3'>
                {type==='basket'?(
                    <div>
                        <button className='f-0' onClick={reduceGoodCount}>-</button>
                        <p className='f-0'>{count}</p>
                        <button className='f-0' onClick={increaceGoodCount}>+</button>
                    </div>
                )
                :
                (
                    <>
                        {!isGoodInBasket?
                            <button onClick={addGoodToBasket} className='add f-0'>Додати</button>
                            :
                            (
                                <div>
                                    <button className='f-0' onClick={reduceGoodCount}>-</button>
                                    <p className='f-0'>{count}</p>
                                    <button className='f-0' onClick={increaceGoodCount}>+</button>
                                </div>
                            )
                        }
                    </>
                )}
            </div>
        </section>
    )
}

export default GoodItem