import './newItem.css'
import image from '../../images/1.jpeg'
import { useDispatch, useSelector } from 'react-redux'
import { openNewAction } from '../../store/newsReduser'

function NewItem(props){
    const {funcs, vars} = props
    const {id, type} = vars
    
    const dispatch = useDispatch()
    const newInfo = useSelector(state => state.news.news[id])
    const openNew = () => {
        dispatch(openNewAction(id))
    }
    

    return(
        <section className='newItem'>

            <p>{newInfo.date}</p>
            <h1>{newInfo.title}</h1>
            {type === 'half'?
            (
                <>
                    <p dangerouslySetInnerHTML={{ __html: newInfo.shortDescription }}></p>
                    <button onClick={openNew} className='buttonLink'>Читати все</button>
                </>
            )
            :
            (
                <p dangerouslySetInnerHTML={{ __html: newInfo.fullDescription }}></p>
            )}
            <img src={newInfo.imageUrl} alt='new icon'></img>
        </section>
    )
}

export default NewItem