export async function getMenuFromPoster(dispatch){
    //звертається на сервер, який звязується із повтером та дає відповідь 
    // categories:{} goods:{}

    const url = 'api/menu/getMenuFromPoster'
    const response = await fetch(url)
    const jsonResponse = await response.json()

    const categories = jsonResponse.categories
    const goods = jsonResponse.goods

    const menu = categories.filter(category => category.level === '1').map((category) => {
        const titleCategory = category.category_name
        const subCategoryies = categories.filter(subCategory => subCategory.level == '2' && subCategory.parent_category === category.category_id).map((subCategory) =>{
            const titleSubCategory = subCategory.category_name
            const goodsSubCategory = goods.filter(good => good.menu_category_id === subCategory.category_id).map((good) =>{
                const posterProductId = good.product_id
                const goodTitle = good.product_name
                const goodDescription = good.product_name
                const goodPrice = good.price['1']
                const goodImage = good.photo
                return {
                    posterProductId: posterProductId,
                    title: goodTitle,
                    description: goodDescription,
                    price: goodPrice,
                    image: goodImage,
                }
            })

            return {
                title: titleSubCategory,
                goods: goodsSubCategory
            }
        })
        return {
            title: titleCategory,
            subCategoryies: subCategoryies
        }
    })

    console.log(menu);
    dispatch(setMenu(menu))

}