import './windowTitleComponent.css'
import { useSelector } from 'react-redux';
import { useEffect, useState } from 'react';

function WindowTitleComponent(props){
    const {vars, funcs} = props

    let title;
    try {({ title } = vars);}
    catch (error) {title = ''}

    const openWindow = useSelector(state => state.window.globalWindowOpen)
    const windowTitle = useSelector(state => state.window.globalWindowData[openWindow].title)

    
    //анімація
    const animationQueueList = [0,1,2,3,4,5,6]
    const [animationQueue, setAnimationQueue] = useState(0)
    async function startAnimation(type){
        switch (type) {
            case 'open':
                for (let i = 0; i < animationQueueList.length; i++) {
                    const item = animationQueueList[i];
                    await new Promise((resolve) => setTimeout(resolve, 50)); // Затримка на 50мс
                    setAnimationQueue(item);
                }
                break;
            case 'close':
                for (let i = animationQueueList.length - 1; i >= 0; i--) {
                    const item = animationQueueList[i];
                    await new Promise((resolve) => setTimeout(resolve, 50)); // Затримка на 50мс
                    setAnimationQueue(item);
                }
                break;
        
            default:
                break;
        }
    }

    useEffect(()=>{
        //запуск анімації
        startAnimation('open')
    },[])
    

    return(
        <>
            {title!==''? <h1 showFrom={animationQueue<3?'bottom':''} elStatus={animationQueue<3?'hide':'show'} className='windowTitle f-2'>{title}</h1> : 
            <h1 showFrom={animationQueue<3?'bottom':''} elStatus={animationQueue<3?'hide':'show'} className='windowTitle f-2'>{windowTitle}</h1>}
        </>
    )
}

export default WindowTitleComponent