import { useDispatch,useSelector } from 'react-redux'
import './basketWindow.css'
import GoodItem from '../goodItem/goodItem'
import CopyrightComponent from '../copyrightComponent/copyrightComponent'
import WindowTitleComponent from '../windowTitleComponent/windowTitleComponent'
import OrderItem from '../orderItem/orderItem'
import ModalWindow from '../modalWindow/modalWindow'

import { clearBasketAction, clearOrdersAction } from '../../store/basketReduser'

import { createOrder, checkOrdersStatus } from '../../servises/orederServises'

import { openGlobalWindowAction } from '../../store/windowReduser'
import { useEffect, useState } from 'react'

function BasketWindow(props){
    const {funcs, vars} = props
    
    const dispatch = useDispatch()
    const openMain = () =>{
        dispatch(openGlobalWindowAction(1))
      }
    
    const basketGoods = useSelector(state => state.basket.basketGoods)
    const orders = useSelector(state => state.basket.orders)
    const menuCategoryies = useSelector(state => state.menu.categoryies)
    
    const [askModal, setAskModal] = useState(null)
    const [alertModal, setAlertModal] = useState(null)

    useEffect(()=>{
        localStorage.setItem('basket', JSON.stringify(basketGoods));
    },[basketGoods])

    useEffect(() => {
        // dispatch(clearOrdersAction(1))
        //якщо користувач робив вже якісь замовлення
        if(orders.length>0){
            const checkOrdersInterval = setInterval(async () => {
            const updatedOrders = await checkOrdersStatus(dispatch, orders);
            }, 3000); // перевірка статусів замовлень кожні 3 секунди
            return () => clearInterval(checkOrdersInterval);
        }
      }, []);


    async function createNewOrder(){
        //викликається модальне вікно для користувача, та очікується на його вибір
        const type = await new Promise((resolve, reject) => {
            const choices = [
                {
                    title: 'Фіат',
                    value: 'monobank'
                },
                {
                    title: 'Кріпта',
                    value: 'bitcoin'
                }
            ]
            setAskModal(<ModalWindow vars={{type:'ask', title:'Виберіть метод оплати:',choices:choices }} funcs={{onChoice:resolve, closeModal:()=>{setAskModal(null)}}}/>)
        });

        if(type==='bitcoin'){
            setAlertModal(<ModalWindow vars={{type:'alert', title:'Цей метод ще не підтрмуєтья',choices:[{title:'Добре', value:true}] }} funcs={{closeModal:()=>{setAlertModal(null)}}}/>)
            return
        }
        createOrder(type, dispatch, {basketGoods, menuCategoryies})
        dispatch(clearBasketAction())
    }



    //анімація
    const animationQueueList = [0,1,2,3,4,5,6]
    const [animationQueue, setAnimationQueue] = useState(0)
    async function startAnimation(type){
        switch (type) {
            case 'open':
                for (let i = 0; i < animationQueueList.length; i++) {
                    const item = animationQueueList[i];
                    await new Promise((resolve) => setTimeout(resolve, 50)); // Затримка на 50мс
                    setAnimationQueue(item);
                }
                break;
            case 'close':
                for (let i = animationQueueList.length - 1; i >= 0; i--) {
                    const item = animationQueueList[i];
                    await new Promise((resolve) => setTimeout(resolve, 50)); // Затримка на 50мс
                    setAnimationQueue(item);
                }
                break;
        
            default:
                break;
        }
    }

    useEffect(()=>{
        //запуск анімації
        startAnimation('open')
    },[])
    


    return(
        <div className='window'>
            {orders.length>0?(
                        <>
                            <WindowTitleComponent vars={{title:'Замовлення'}}/>
                            <section showFrom={animationQueue<3?'bottom':''} elStatus={animationQueue<3?'hide':'show'} className='orderList'>
                                {orders.map(order => <OrderItem vars={{order:order, id:order.id, status:order.status}}/>)}
                            </section>
                        </>
                    )
                :
                    (
                        <></>
                    )}

                <WindowTitleComponent />
            <article id='basketWindow' >

                {askModal!==null&&askModal}
                {alertModal!==null&&alertModal}

                {basketGoods.length<=0?
                    (<section className='basketNull'>
                        <p showFrom={animationQueue<4?'bottom':''} elStatus={animationQueue<4?'hide':'show'}>Ваш кошик пустий ;(</p>
                        <button showFrom={animationQueue<5?'bottom':''} elStatus={animationQueue<5?'hide':'show'} onClick={openMain}>На головну</button>
                    </section>)
                    :
                    (
                        <div className='window-flex-column' showFrom={animationQueue<6?'bottom':''} elStatus={animationQueue<6?'hide':'show'}>
                            {basketGoods.map((basketGood) => {
                                const good = menuCategoryies[basketGood.category].subCategoryies[basketGood.subCategory].goods[basketGood.goodId]
                                return <GoodItem vars={{type:'basket', title: good.title, description: good.description, price: good.price, img: good.image, category:basketGood.category, subCategory:basketGood.subCategory, goodId:basketGood.goodId }}/>
                            })}

                            <button onClick={createNewOrder} className='done'>Оформити замовлення</button>
                        </div>
                    )
                }
            </article>
            <CopyrightComponent vars={{color:'black'}}/>
        </div>
    )
}

export default BasketWindow