import './galary.css'

import { useState, useEffect } from 'react'

function Galary(props){
    const {funcs, vars} = props
    // const {} = functions
    const {itemsCount} = vars
    
    const[itemNum, setIterNum] = useState(-1)
    const[glaryBoxOffset, setGlaryBoxOffset] = useState('0px')
    
    const calculateOffsets = (nexItemNum) => {
        const item = document.querySelectorAll('.galaryItem')[nexItemNum]
        const galary = document.querySelector('.galary')
        const glaryBox = document.querySelector('.glaryBox')
        const galaryOffsetLeftCenter = (galary.offsetWidth || 2) / 2
        const glaryBoxOffsetCenter = glaryBox.offsetLeft - galaryOffsetLeftCenter
        const glaryBoxOffsetLeft = glaryBox.offsetLeft
        const itemOffsetLeftCenter = item.offsetLeft + item.offsetWidth / 2
        return glaryBoxOffsetLeft-(glaryBoxOffsetCenter+itemOffsetLeftCenter)
    }

    const nextItem = (way) => {
        
        if(way === 'back'){
            const nexItemNum = itemNum>0?itemNum-1:(itemsCount-1)
            Array.from(document.querySelectorAll('.galaryItem')).map((el, idx)=>{
                if(idx===nexItemNum)el.classList.add('bigItem')
                else el.classList.remove('bigItem')
            })
            setGlaryBoxOffset(calculateOffsets(nexItemNum))
            setIterNum(nexItemNum)
        }
        if(way === 'forward'){
            const nexItemNum = itemNum<(itemsCount-1)?itemNum+1:0
            Array.from(document.querySelectorAll('.galaryItem')).map((el, idx)=>{
                if(idx===nexItemNum)el.classList.add('bigItem')
                else el.classList.remove('bigItem')
            })
            setGlaryBoxOffset(calculateOffsets(nexItemNum))
            setIterNum(nexItemNum)
        }
    }
    
      useEffect(() => {
        setTimeout(() => {
            nextItem('forward')
        }, 2000);
      }, [nextItem]);

      useEffect(() => {
        nextItem('forward')
      }, []);


    //анімація
    const animationQueueList = [0,1]
    const [animationQueue, setAnimationQueue] = useState(0)
    async function startAnimation(type){
        switch (type) {
            case 'open':
                for (let i = 0; i < animationQueueList.length; i++) {
                    const item = animationQueueList[i];
                    await new Promise((resolve) => setTimeout(resolve, 50)); // Затримка на 50мс
                    setAnimationQueue(item);
                }
                break;
            case 'close':
                for (let i = animationQueueList.length - 1; i >= 0; i--) {
                    const item = animationQueueList[i];
                    await new Promise((resolve) => setTimeout(resolve, 50)); // Затримка на 50мс
                    setAnimationQueue(item);
                }
                break;
        
            default:
                break;
        }
    }

    useEffect(()=>{
        //запуск анімації
        startAnimation('open')
    },[])



    return (
        <div showfrom={animationQueue<1?'top':''} elstatus={animationQueue<1?'hide':'show'} className='galary'>
            <div style={{left:`${glaryBoxOffset}px`}} className='glaryBox'>
                {props.children}
            </div>
            <div className='doters'>
                {Array(itemsCount).fill(0).map((_,idx)=>{
                    return <span key={idx} className={itemNum===idx?'fill':''}></span>
                })}
            </div>
        </div>
    )
}

export default Galary