import './orderItem.css'
import { useDispatch } from 'react-redux'
import { deleteOrderAction } from '../../store/basketReduser'
import { deleteOrderFromServer } from '../../servises/orederServises'

function OrderItem(props){
    const {funcs, vars} = props
    const {order, id, status} = vars
    const dispatch = useDispatch()

    async function deleteOrder(orderId){
        // await deleteOrderFromServer(orderId)
        dispatch(deleteOrderAction(order))
    }

    return(
        <section className='orderItem'>
            <p className='orderId f-0'>#{id}</p>
            {status==='pay' && (<p className='orderStatus f-1'>Очікується оплата...</p>)}
            {status==='waiting for approve' && (<p className='orderStatus f-1'>Касир перевіряє Ваше замовлення...</p>)}
            {status==='approved' && (<p className='orderStatus f-1'>Ваше замовлення вже готується, очікуйте :)</p>)}
            {status==='done' && (
                <>
                    <p className='orderStatus f-1'>Готово! Підійдіть на бар та покажіть ID: #{id}</p>
                    <button onClick={()=>{deleteOrder(id)}} className='f-0'>Отримано</button>
                </>
            )}
            {status==='prepearing' && (<p className='orderStatus f-1'>Готується, слідкуйте за таблом видачі :)</p>)}
            {/* <span style={{fontSize:'10vh'}} className='material-icons'>credit_card</span> */}
        </section>
    )
}

export default OrderItem